import React, { Component } from 'react';

import {
    Label,
    Button,
    Form,
    FormGroup,
    Modal,
    ModalFooter,
    Input,
    ModalBody,
    ModalHeader
} from 'reactstrap'
class HireMe extends Component {
  constructor(props) {
    super(props);
    this.state = {
        visitorInfo:null
    };
  }


  handleChange = (e) => {

    const {name, value} = e.target;
    const visitorInfo = {...this.state.visitorInfo, [name]:value}
    this.setState({visitorInfo})



  }

  render() {

    const {onSubmite, toggle} = this.props;
    return (

        <Modal isOpen={true} toggle={toggle}>
            <ModalHeader toggle={toggle} className="bg-light text-dark">Hire Me :)</ModalHeader>
         



                       <ModalBody className="bg-white">
                         <Form>


                             <FormGroup>
                                <Label>Email</Label>
                                    <Input 
                                    name="email"
                                    type="email"
                                    required={true}
                                    placeholder="Your email address"
                                    onChange={this.handleChange}

                                    />
                             </FormGroup>




                         </Form>
                
                     </ModalBody>

            <ModalFooter className="bg-light">
                <Button className="btn btn-info text-white" onClick={() => onSubmite(this.state.visitorInfo)}>Hire</Button>
            </ModalFooter>
        </Modal>
      
    );
  }
}

export default HireMe;
