import React, { Component } from 'react';

import {
  Label,
  Button,
  Form,
  FormGroup,
  Modal,
  ModalFooter,
  Input,
  ModalBody,
  ModalHeader
} from 'reactstrap'
class SignUp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visitorInfo: null
    };
  }


  handleChange = (e) => {

    const { name, value } = e.target;

    const visitorInfo = { ...this.state.visitorInfo, [name]: value }
    this.setState({ visitorInfo })



  }

  render() {

    const { onSubmite, toggle } = this.props;
    return (

      <Modal isOpen={true} toggle={toggle}>
        <ModalHeader toggle={toggle} className="bg-light text-dark">Let Me Know You Little Bit </ModalHeader>




        <ModalBody className="bg-white">
          <Form>


            <FormGroup>
              <Label>Name</Label>
              <Input
                name="name"
                type="text"
                required={true}
                placeholder="Your Full Name"
                onChange={this.handleChange}

              />
            </FormGroup>




            <FormGroup>
              <Label>Email</Label>
              <Input
                name="email"
                type="email"
                required={true}
                placeholder="Your Email"
                onChange={this.handleChange}
              />
            </FormGroup>




            <FormGroup>
              <Label>Who Are You</Label>
              <Input
                name="who"
                type="text"
                required={true}
                placeholder="Just 5 words"
                onChange={this.handleChange}
              />
            </FormGroup>
          </Form>

        </ModalBody>

        <ModalFooter className="bg-light">
          <Button className="btn btn-info text-white" onClick={() => {
            window.open('https://webzacky.onrender.com/')
          }}>Demo</Button>
        </ModalFooter>
      </Modal>

    );
  }
}

export default SignUp;
